'use client'
import { getBoards, postBoards } from '@/services/BoardsService'
import React, { useEffect, useState } from 'react'

function SideBar() {

    const [boards, setBoards] = useState([])

    const fetchBoards = async () =>{
            try{
                const boards = await getBoards()
                setBoards(boards)
            }catch(e){
            console.error(e)
        }
        }

    useEffect(()=>{
        fetchBoards()
    }, [])

    const addBoard = async () =>{
        try{
            const newBoard = await postBoards()
            if (newBoard) setBoards(prev => [...prev, newBoard])
        }catch(e){
            console.error(e)
        }
    }
  return (
    <div className='flex justify-between  w-64 flex-col h-full pl-10 pr-6 border-r border-gray-200'>
        <div>
            <div className='mt-6'>
                meybe logo here
            </div>
            <ul className='mt-6'>
                {boards.length > 0 ? (
                    boards.map((board) =>(
                            <li key={board.id} className='border-b border-gray-200'>{board.title}</li>
                    ))
                ): (
                    <li>no boards</li>
                )}
                <li>
                    <button onClick={()=>{addBoard()}} className='rounded-xl mt-2 hover:bg-blue-400 p-2'> + Add New Board</button>
                </li>
            </ul>
        </div>

        <div className='mb-12'>
            cia bus logout
        </div>
    </div>
  )
}

export default SideBar