import { TaskPost } from "@/types/types";
import axios from "axios";
import 'dotenv/config'

const url = process.env.NEXT_PUBLIC_TODO_API

export const getToDo = async() =>{
    try{
        const urlWithBoardId = url + `/${1}`
        if(!url){
            throw new Error('no url provided')
        }
        const tasks = await axios.get(urlWithBoardId)
        return tasks.data
        
    }catch(e){
        console.error(e)
    }
}

export const postToDo = async (data: TaskPost) =>{
    try{
        const fuckthis = process.env.NEXT_PUBLIC_TODO_API_1
        const res = await axios.post(fuckthis,{
            title: data.title,
            dueDate: new Date(data.dueDate).toISOString(),
            description: data.description,
            boardId: 1
        },
        {
            headers:{
            "Content-Type": "application/json"
            }
        }
    )
    }catch(e){
        console.error(e)
    }
}

export const deleteTodo = async (id: number) => {
    try{
        const urlWithId = url + `/${id}`
        const deleteTodo = axios.delete(urlWithId)
    }catch(e){
        console.log(e)
    }
}