'use client'
import SideBar from '@/components/SideBar'
import ToDoList from '@/components/ToDoList'
import ToolBar from '@/components/ToolBar'
import TopBar from '@/components/TopBar'
import React, { useState, useEffect } from 'react'
import NewTaskModal from '@/components/NewTaskModal'
import { Task } from '@/types/types'
import { getToDo, postToDo, deleteTodo } from '@/services/ToDoServices'

function page() {
  const [showModal, setShowModal] = useState(false)
  const [todo, setTodo] = useState<Task[]>([])
  const [task, setTask] = useState<TaskPost>({title: '', dueDate: '', description: ''})
  const [selectedForDelete, setSelectedForDelete] = useState('')

  const fetchTasks = async() =>{
      try{
        const data = await getToDo()
        setTodo(data)
      }catch(e){
        console.log(e)
      }
    }

  const handleSubmit = async () =>{
          try{
              await postToDo(task)
              setShowModal(!showModal)
              fetchTasks()
          }catch(e){
              console.log(e)
          }
    }
  
    const deleteTask = async (id: string) =>{
      try{
        await deleteTodo(Number(id))
        fetchTasks()
      }catch(e){
        console.log(e)
      }
    }

  useEffect(()=>{
    fetchTasks()
  },[])
  return (
    <div className='flex h-screen bg-blue'>
      <div className='z-50'>
          <NewTaskModal
          show={showModal}
          handleSubmit = {handleSubmit}
          setTask={setTask}
          task={task}
          closeModal={setShowModal}
        />
      </div>
        <div>
            <SideBar/>
        </div>
        <div className='w-full overflow-scroll bg-gray-100'>
            <TopBar/>
            <ToolBar 
              setShowModal={setShowModal}
            />
              <ToDoList 
                deleteTask={deleteTask}
                todo={todo}
              />
        </div>
    </div>
  )
}

export default page